<template>
  <v-layout justify-center column>
    <div class="xs-top">

    </div>
    <div class="text-xs-center">

      <v-avatar
        :tile="tile"
        :size="avatarSize"
        color="grey lighten-4"
        class="elevation-15"
      >
        <img src="https://lestat.b0.upaiyun.com/blog/avatar.jpg">
      </v-avatar>
      <h1 class="text-xs-top">
        <code id="h1" class="black h1 grey--text text--lighten-3 elevation-15"></code>
      </h1>
      <v-btn :key="i" v-for="(item,i) in links" flat @click="jumpTo(item[1])">
        <v-icon>{{item[2]}}</v-icon>
        {{item[0]}}
      </v-btn>
      <v-dialog v-model="dialog" fullscreen hide-overlay transition="dialog-bottom-transition">
        <v-btn slot="activator" flat>留言板
          <v-icon>dashboard</v-icon>
        </v-btn>
        <v-card>
          <v-toolbar dark color="secondary">
            <v-btn icon @click.native="dialog = false">
              <v-icon>close</v-icon>
            </v-btn>
            <v-toolbar-title>留言板</v-toolbar-title>
            <v-spacer></v-spacer>
            <v-toolbar-items>
              <v-btn dark flat @click.native="dialog = false">保存</v-btn>
            </v-toolbar-items>
          </v-toolbar>
          <v-list three-line subheader>
            <v-subheader>正在开发中...</v-subheader>
            <v-list-tile avatar>
              <v-list-tile-content>
                <v-list-tile-title>Content filtering</v-list-tile-title>
                <v-list-tile-sub-title>Set the content filtering level to restrict apps that can be downloaded
                </v-list-tile-sub-title>
              </v-list-tile-content>
            </v-list-tile>
            <v-list-tile avatar>
              <v-list-tile-content>
                <v-list-tile-title>Password</v-list-tile-title>
                <v-list-tile-sub-title>Require password for purchase or use password to restrict purchase
                </v-list-tile-sub-title>
              </v-list-tile-content>
            </v-list-tile>
          </v-list>
          <v-divider></v-divider>
          <v-list three-line subheader>
            <v-subheader>General</v-subheader>
            <v-list-tile avatar>
              <v-list-tile-action>
                <v-checkbox v-model="notifications"></v-checkbox>
              </v-list-tile-action>
              <v-list-tile-content>
                <v-list-tile-title>Notifications</v-list-tile-title>
                <v-list-tile-sub-title>Notify me about updates to apps or games that I downloaded
                </v-list-tile-sub-title>
              </v-list-tile-content>
            </v-list-tile>
            <v-list-tile avatar>
              <v-list-tile-action>
                <v-checkbox v-model="sound"></v-checkbox>
              </v-list-tile-action>
              <v-list-tile-content>
                <v-list-tile-title>Sound</v-list-tile-title>
                <v-list-tile-sub-title>Auto-update apps at any time. Data charges may apply</v-list-tile-sub-title>
              </v-list-tile-content>
            </v-list-tile>
            <v-list-tile avatar>
              <v-list-tile-action>
                <v-checkbox v-model="widgets"></v-checkbox>
              </v-list-tile-action>
              <v-list-tile-content>
                <v-list-tile-title>Auto-add widgets</v-list-tile-title>
                <v-list-tile-sub-title>Automatically add home screen widgets</v-list-tile-sub-title>
              </v-list-tile-content>
            </v-list-tile>
          </v-list>
        </v-card>
      </v-dialog>
    </div>
  </v-layout>

</template>

<style>
  @keyframes blink {
    from {
      opacity: 0;
    }
    to {
      opacity: 1;
    }
  }

  #h1::after {
    content: '|';
    animation: blink 500ms infinite;
  }

  #h1 {
    flex-grow: 1;
  }

  h1 {
    margin: 30px;
  }

</style>


<script>
  import theaterJS from 'theaterJS'

  export default {
    name: 'Helloworld',
    data: () => ({
      dialog: false,
      notifications: false,
      sound: true,
      widgets: false,
      slider: 168,
      tile: false,
      links: [['博客', 'http://lestatmiao.github.io/', 'home'], ['Github', 'https://github.com/lestatmiao/', 'code'], ['关于', 'https://lestatmiao.github.io/about/', 'face'], ['中文维基(PC)', 'https://wiki.lestat.me/', 'pages']],
      lorem: 'Lorem ipsum dolor sit amet, at aliquam vivendum vel, everti delicatissimi cu eos. Dico iuvaret debitis mel an, et cum zril menandri. Eum in consul legimus accusam. Ea dico abhorreant duo, quo illum minimum incorrupte no, nostro voluptaria sea eu. Suas eligendi ius at, at nemore equidem est. Sed in error hendrerit, in consul constituam cum.'
    }),
    computed: {
      avatarSize() {
        return `${this.slider}px`
      }
    },
    mounted() {
      const theater = theaterJS()
      //console.log(document.getElementById('h1'))

      theater.addActor('h1', {speed: 0.8, accuracy: 0.6})
      theater.addScene(1800)
      theater.addScene("h1:什么都略懂一点", 800)
      theater.addScene(800)
      theater.addScene("h1:生活更多彩一些", 800)
      theater.addScene(800)
      theater.addScene(theater.replay.bind(theater))
    },
    methods: {
      jumpTo(url) {
        window.open(url)
      }
    }
  }
</script>



