<template>
  <v-app :dark="dark">
    <router-view/>
    <div>
      <v-btn outline large class="elevation-15" :class="textClass" @click="toggleTheme()" :color="textColor">{{colorText}}</v-btn>
    </div>
  </v-app>
</template>

<script>
export default {
  name: 'App',
  data() {
    return {
      dark: true,
      colorText: '白天',
      textColor: 'white',
      textClass: 'white--text',
      mainStyle: 'dark'
    }
  },
  methods:{
    toggleTheme() {
      this.dark = !this.dark
      this.colorText = this.colorText === '白天' ? '夜晚' : '白天' ;
      this.textColor = this.textColor === 'white' ? 'black' : 'white' ;
      this.textClass = this.textClass === 'white--text' ? 'black--text' : 'white--text' ;
    }
  }
}
</script>

<style>

</style>



